body {
  background: linear-gradient(135deg, #a1ffce 0%, #faffd1 100%);
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  font-family: Poppins, sans-serif;
  display: flex;
}

.blog-title {
  color: #07f;
  text-align: center;
  text-shadow: 2px 2px 5px #0077ff4d;
  margin-bottom: 20px;
  font-size: 3rem;
}

.create-post-form {
  background: linear-gradient(135deg, #00c3ff 0%, #ffff1c 100%);
  border-radius: 16px;
  flex-direction: column;
  gap: 18px;
  width: 100%;
  max-width: 500px;
  margin-bottom: 40px;
  padding: 25px 30px;
  display: flex;
  box-shadow: 0 8px 20px #0003;
}

.title-input, .content-input {
  background: #fff;
  border: none;
  border-radius: 10px;
  width: 100%;
  padding: 14px 18px;
  font-size: 1rem;
  box-shadow: inset 0 0 5px #0000001a;
}

.title-input::placeholder, .content-input::placeholder {
  color: #666;
}

.create-post-btn {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #38ef7d 0%, #11998e 100%);
  border: none;
  border-radius: 10px;
  padding: 14px 18px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: transform .3s, background .3s;
}

.create-post-btn:hover {
  background: linear-gradient(135deg, #11998e 0%, #38ef7d 100%);
  transform: scale(1.05);
}

.posts-container {
  flex-direction: column;
  gap: 25px;
  width: 100%;
  max-width: 600px;
  display: flex;
}
