body {
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(135deg, #a1ffce 0%, #faffd1 100%);
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
  }
  
  .blog-title {
    font-size: 3rem;
    color: #0077ff;
    margin-bottom: 20px;
    text-align: center;
    text-shadow: 2px 2px 5px rgba(0, 119, 255, 0.3);
  }
  
  .create-post-form {
    background: linear-gradient(135deg, #00c3ff 0%, #ffff1c 100%);
    padding: 25px 30px;
    border-radius: 16px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
    width: 100%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    gap: 18px;
    margin-bottom: 40px;
  }
  
  .title-input,
  .content-input {
    width: 100%;
    padding: 14px 18px;
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    background: #ffffff;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .title-input::placeholder,
  .content-input::placeholder {
    color: #666;
  }
  
  .create-post-btn {
    padding: 14px 18px;
    font-size: 1.1rem;
    background: linear-gradient(135deg, #38ef7d 0%, #11998e 100%);
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: transform 0.3s, background 0.3s;
  }
  
  .create-post-btn:hover {
    transform: scale(1.05);
    background: linear-gradient(135deg, #11998e 0%, #38ef7d 100%);
  }
  
  .posts-container {
    width: 100%;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }